<template>
  <div class="top">
   <div class="actorItem" v-for="item in actor">
     <div class="actorcontent" v-html="item.content"></div>
     <div class="actortime">{{item.creatTime}}</div>
   </div>

    <pagination :page="pagemess.page" :totalpage="pagemess.totalpage" @changepage="gopage"></pagination>
  </div>
</template>

<script>
  import pagedata from '@/dataJson/dongtai'
  import photo from "@/assets/photo.png"
  import pagination from  "@/components/comom/pagination.vue"

  export default {
    components:{
      pagination,
    },
    data () {
      return {
        pagemess:{
          totalpage:0,
          page:1
        },
        actor:[]
      }
    },
    mounted:function () {
      this.getdata()

    },
    methods:{

      gopage:function (page) {
        this.$router.push({path:"project",query:{page:page}});
        this.actor = [];
        this.getdata()
      },
      getdata:function () {

        let page = this.$route.query.page;
        this.pagemess.totalpage = pagedata.length;
        if(!page){
          this.pagemess.page = 1;
          this.actor = pagedata[0]
        }else {
          if(page> pagedata.length || page<1){
            this.pagemess.page = 1;
            this.actor = pagedata[0]
            return
          }
          this.pagemess.page = parseInt(page);
          this.actor = pagedata[page-1];
        }
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .actorItem{
    width: 100%;
    background-color: #fff;
    padding: 16px;
    box-sizing: border-box;
    border-radius: 2px;
    margin-bottom: 20px;
    font-size: 16px;
  }
  .actorcontent{

  }
  .actortime{
    text-align: right;
    font-size: 14px;
    color: #888;
  }
</style>



